import { Container } from '@mui/material';
import { ProfileStyled } from 'components/Profile/Profile';
import { Section } from 'components/Section/Section.styled';
import { useSelector } from 'react-redux';
import { selectoAboutContact } from 'redux/selectors';

export const Profile = () => {
  const { avatarURL, email, name } = useSelector(selectoAboutContact);

  return (
    <>
      <main>
        <Container>
          <Section>
          <ProfileStyled avatarURL={avatarURL} email={email} name={name}/>
          </Section>
        </Container>
      </main>
    </>
  );
};
